export default ()=> {
      return (
        <div className="question" data-index="120" dangerouslySetInnerHTML = {{ __html: `
<h6>120. 输出什么？</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> groceries <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token string">"banana"</span><span class="token punctuation">,</span> <span class="token string">"apple"</span><span class="token punctuation">,</span> <span class="token string">"peanuts"</span><span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token keyword keyword-if">if</span> <span class="token punctuation">(</span>groceries<span class="token punctuation">.</span><span class="token function">indexOf</span><span class="token punctuation">(</span><span class="token string">"banana"</span><span class="token punctuation">)</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
	console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"We have to buy bananas!"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span> <span class="token keyword keyword-else">else</span> <span class="token punctuation">{</span>
	console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token template-string"><span class="token template-punctuation string">\`</span><span class="token string">We don't have to buy bananas!</span><span class="token template-punctuation string">\`</span></span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span>
</code></pre>
<ul>
<li>A: We have to buy bananas!</li>
<li>B: We don't have to buy bananas</li>
<li>C: <code>undefined</code></li>
<li>D: <code>1</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p>我们传递了一个状态 <code>groceries.indexOf(&quot;banana&quot;)</code> 给if条件语句。<code>groceries.indexOf(&quot;banana&quot;)</code> 返回 <code>0</code>， 一个 falsy 的值。因为if条件语句的状态为 falsy，<code>else</code> 块区内的代码执行，并且 <code>We don't have to buy bananas!</code> 被输出.</p>
</p>
</details>
` }}></div>
      );
    }